@import url(~bootstrap-datepicker/dist/css/bootstrap-datepicker.min.css);

.PublicChat .fetchMoreButton,
.PublicChat .fetchingMessages,
.PublicChat .allMessagesFetched {
  margin: 0px auto;
  display: flex;
  justify-content: center;
  padding: 10px 0;
  text-align: center;
  font-size: 1.8rem;
}

.PublicChat .fetchMoreButton {
  cursor: pointer;
}

.PublicChat .fetchMoreButton .material-icons {
  line-height: 27px;
  font-size: 24px;
  margin-left: 5px;
  position: relative;
}

.PublicChat__textarea.form-control {
  height: 42px;
}

.PublicChat__timeAgo {
  color: #ce5338;
}

.PublicChat__panel {
  padding: 0;
}

.PublicChat__chat {
  list-style: none;
  margin: 0;
  padding: 0;
}

.PublicChat__chat li {
  margin-bottom: 10px;
  padding-bottom: 5px;
  border-bottom: 1px dashed #bfbfbf;
}

.PublicChat__chat li:last-of-type {
  border-bottom: 0;
}

.PublicChat__body {
  margin-left: 60px;
}

.PublicChat__body p {
  font-size: 1.6rem;
  line-height: 2.1rem;
  color: #524e4d;
  font-style: italic;
}

.PublicChat__panelBody {
  position: relative;
  overflow-y: scroll;
  height: 400px;
  padding: 15px;
}

.PublicChat__profilePicture {
  border-radius: 50%;
  -o-object-fit: cover;
  object-fit: cover;
  width: 50px;
  height: 50px;
}

.PublicChat__header a {
  color: #555;
  font-weight: 500;
}

.PublicChat__header a:hover {
  color: #216a94;
  text-decoration: none;
}

.PublicChat__panelFooter {
  background-color: white;
  border-top: 1px solid #ccd0d2;
}

.PublicChat__panelFooter .btn {
  color: #fff;
  border-radius: 6px;
  background-color: #d3654c;
  -webkit-box-shadow: 1px 2px 2px rgba(0, 0, 0, 0.3);
  box-shadow: 1px 2px 2px rgba(0, 0, 0, 0.3);
}

.PublicChat__panelFooter .btn:hover {
  background-color: #ce5338;
}

.PublicChat__panelFooter textarea {
  resize: none;
}

.PublicChat__countChars {
  background-color: #b1b7ba;
  margin-top: 7px;
  display: inline-block;
}

.PublicChat__submitButton {
  margin-top: 10px;
}




